<template>
	<div class="zstp_fg_page3">
		<div class="cont box-border-shadow">
			<div style="margin: 0 auto 25px;width: 60%">
				<el-autocomplete style="width: 100%" v-model="form.input" :fetch-suggestions="querySearchAsync"
				  placeholder="请输入内容"
				  @select="handleSelect" 
				  clearable
				>
					<el-select v-model="form.select" slot="prepend" placeholder="请选择类别" style="width: 200px;">
				      <el-option label="勘察单位" value="勘察单位"></el-option>
				      <el-option label="设计单位" value="设计单位"></el-option>
				      <el-option label="招标代理单位" value="招标代理单位"></el-option>
				      <el-option label="招标、定标方式" value="招标、定标方式"></el-option>
				      <el-option label="造价咨询单位" value="造价咨询单位"></el-option>
				      <el-option label="合同价" value="合同价"></el-option>
				       <el-option label="施工单位" value="施工单位"></el-option>
				      <el-option label="监理单位" value="监理单位"></el-option>
				      <el-option label="造价初审单位" value="造价初审单位"></el-option>
				      <el-option label="存在的问题" value="存在的问题"></el-option>
				      <el-option label="相关法律法规" value="相关法律法规"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search" @click="showGraph()"></el-button>
				</el-autocomplete>
			</div>
	    	<div v-show="!showGrap" style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
				<i class="am-icon-spinner am-icon-spin"></i> loading
			</div>
			<div v-show="showGrap=='暂无数据'">
				<img style="display:block;margin: 20px auto;" src="../../../assets/no-sj.png">
			</div>
	  		<div v-show="showGrap&&showGrap!='暂无数据'" id="graphCharts" :style="{width:windowWidth+'px'}" style="margin:0 auto; height: 800px;text-align: center;"></div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	// import './../image/jquery.easyui.min.js'
	export default{
		name:'zstp_fg_page3',
		data(){
			return {
			    showGrap:'暂无数据',
			    form: {
		          select:'',
		          input:''
		        },
		        timeout:  null,
		        restaurants: [],
		        windowWidth:(window.innerWidth)*0.85,
			}
		},
		components:{
		},
		computed:{
			company(){
				return JSON.parse(sessionStorage.getItem("company"));
			},
			userName(){
				if(sessionStorage.getItem('username')){
					return sessionStorage.getItem('username');
				}
			},
			// windowWidth(){
			// 	let width=(window.innerWidth)*0.85;
			// 	$(window).resize(function(){
			// 		width=(window.innerWidth)*0.85;
			// 		console.log(width)
			//     })
				
			// 	return width;
			// }
		},
		methods:{
			loadAll() {
		        var result=[];
			 	reqData
			      .req({
			        apiName: "gongchengshenji_chaxun_tuijian",
			        data: {			        
			            text:this.form.input?this.form.input:',',
					    leibie:this.form.select?this.form.select:','
			        }
			      })
			      .then(res => {
			        res = res.data;
			        if (res && res.code === "200") {
			          let data = res.data;
			          console.log(data);
			          for(var i=0;i<data.length;i++){
			          	result.push({value:data[i][0],id:data[i][1]});
			          }
			          console.log(result);
			          this.restaurants=result;
			        }
			      })
			      .catch(err => {
			        console.log(err);
			      });
		    },
		    querySearchAsync(queryString, cb) {
		    	this.loadAll();
		        // var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
		        clearTimeout(this.timeout);
		        this.timeout = setTimeout(() => {
		        	var restaurants = this.restaurants;
		            cb(restaurants);
		        }, 2000 * Math.random());
		        // cb(restaurants);
		    },
		    createStateFilter(queryString) {
		        return (state) => {
		          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
		        };
		    },
		    handleSelect(item) {
		        console.log(item);
		        this.form.select=item.id;
		        
		    },
			unique(arr){
	   　　　 	var newArr = [arr[0].name];
		   		var data=[arr[0]];
		   		//去重复项
		  　　 	for(var i=1;i<arr.length;i++){
		　　　　		if(newArr.indexOf(arr[i].name) == -1){
		          　　  newArr.push(arr[i].name);
		          	    data.push(arr[i]);
		    　　  	}
	    		}
	    		//区分值和主题
	    		for(var i=0;i<data.length;i++){
	    		 	if(data[i].des&&data[i].des==='案例'){
	    		 		data[i].category="案例";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='勘察单位'){
	    		 		data[i].category="勘察单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='设计单位'){
	    		 		data[i].category="设计单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#ffd86e',
				                 borderColor:'#edba39',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='招标代理单位'){
	    		 		data[i].category="招标代理单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='招标、定标方式'){
	    		 		data[i].category="招标、定标方式";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#fb95af',
				                 borderColor:'#e3869d',
				                 borderWidth:2,
				             }
				         }
	    		 	}
	    		 	else if(data[i].des&&data[i].des==='造价咨询单位'){
	    		 		data[i].category="造价咨询单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#de9bf9',
				                 borderColor:'#bf85d6',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='合同价'){
	    		 		data[i].category="合同价";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#859bd6',
				                 borderColor:'#6485d6',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='施工单位'){
	    		 		data[i].category="施工单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#adbcc5',
				                 borderColor:'#8fb0c5',
				                 borderWidth:2,
				             }
				         }
	    		 	}



	    		 	else if(data[i].des&&data[i].des==='监理单位'){
	    		 		data[i].category="监理单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#d48265',
				                 borderColor:'#ad5738',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='造价初审单位'){
	    		 		data[i].category="造价初审单位";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#a38fc5',
				                 borderColor:'#83739e',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='存在的问题'){
	    		 		data[i].category="存在的问题";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#eac2aa',
				                 borderColor:'#c3a28e',
				                 borderWidth:2,
				             }
				         };
				         data[i].symbol="triangle"
	    		 	}else if(data[i].des&&data[i].des==='相关法律法规'){
	    		 		data[i].category="相关法律法规";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#749f83',
				                 borderColor:'#457857',
				                 borderWidth:2,
				             }
				         };
				         data[i].symbol="rect"
	    		 	}
	    		}
	    		console.log(data);
		        return data;
		    },
			graphOption(nodes,links){
		    	console.log(nodes);
		    	console.log(links)
		    	let option = {
				     title: {
				         text: ''
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				      label: {
				        normal: {
				            show: true,
				            textStyle: {
				                fontSize: 22
				            },
				        }
				    },
				     legend: {
				        x: "center",
				        textStyle:{
				        	fontSize:16,
				        },
				        data: [
				        {name:'案例',icon:'circle'},
				        {name:"勘察单位",icon:'circle'}, 
				        {name:"设计单位",icon:'circle'}, 
				        {name:'招标代理单位',icon:'circle'},
				        {name:'招标、定标方式',icon:'circle'},
				        {name:'造价咨询单位',icon:'circle'},
				        {name:'合同价',icon:'circle'},
				        {name:'施工单位',icon:'circle'},
				        {name:'监理单位',icon:'circle'},
				        {name:'造价初审单位',icon:'circle'},
				        {name:'存在的问题',icon:'triangle'},
				        {name:'相关法律法规',icon:'rect'}]
				    },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 60,
				         roam: true,
				         categories: [{
			                name: '案例',
			                itemStyle: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				            }
			             }, {
			                name: '勘察单位',
			                itemStyle: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				            }
			             }, {
			                name: '设计单位',
			                itemStyle: {
				                 color: '#ffd86e',
				                 borderColor:'#edba39',
				                 borderWidth:2,
				            }
			             }, {
			                name: '招标代理单位',
			                itemStyle: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2,
				            }
			             },
			             {
			                name: '招标、定标方式',
			                itemStyle: {
				                 color: '#fb95af',
				                 borderColor:'#e3869d',
				                 borderWidth:2,
				            }
			             },
			             {
			                name: '造价咨询单位',
			                itemStyle: {
				                 color: '#de9bf9',
				                 borderColor:'#bf85d6',
				                 borderWidth:2,
				            }
			             }, {
			                name: '合同价',
			                itemStyle: {
				                 color: '#859bd6',
				                 borderColor:'#6485d6',
				                 borderWidth:2,
				            }
			             }, {
			                name: '施工单位',
			                itemStyle: {
				                 color: '#adbcc5',
				                 borderColor:'#8fb0c5',
				                 borderWidth:2,
				            }
			             }, 



			             {
			                name: '监理单位',
			                itemStyle: {
				                 color: '#d48265',
				                 borderColor:'#ad5738',
				                 borderWidth:2,
				            }
			             },
			             {
			                name: '造价初审单位',
			                itemStyle: {
				                 color: '#a38fc5',
				                 borderColor:'#83739e',
				                 borderWidth:2,
				            }
				        }, {
			                name: '存在的问题',
			                itemStyle: {
				                 color: '#eac2aa',
				                 borderColor:'#c3a28e',
				                 borderWidth:2,
				            },
				            symbol:"triangle"
			             }, {
			                name: '相关法律法规',
			                itemStyle: {
				                 color: '#749f83',
				                 borderColor:'#457857',
				                 borderWidth:2,
				            },
				            symbol:"rect"
			             }],
				         edgeSymbol: ['none', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 2000,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [16, 30],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         // itemStyle: {
				         //     normal: {
				         //         color: '#ff756e',
				         //         borderColor:'#e06760',
				         //         borderWidth:2,
				         //     },

				         // },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000',
				                 curveness:0.2
				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 18,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#000',
						            fontSize: 18,
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		    },
			showGraph(){
				let _this=this;
				_this.showGrap=false;
				if(!this.form.select){
		    		alert("请选择类别");
		    	}else if(!this.form.input){
		    		alert("请输入检索内容");
		    	}else{
		    		reqData
				      .req({
				        apiName: "gongchengshenji_chaxun_zhishitupu",
				        data: {			        
				            text:this.form.input?this.form.input:',',
						    leibie:this.form.select?this.form.select:','
				        }
				      })
				      .then(res => {
				        res = res.data;
				        if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				            let nodes=data.node
							let links=data.links;
							if(nodes.length){
								_this.showGrap=true;
								nodes=_this.unique(nodes);
								console.log(nodes);
								// links=this.linksFormat(links);
								
								var graphCharts = echarts.init(document.getElementById('graphCharts'));
					 	 // 		graphCharts.showLoading();
								// graphCharts.hideLoading();
						    	graphCharts.setOption(_this.graphOption(nodes,links)); 
					    	    $(window).resize(function(){
									graphCharts.resize({
										width:(window.innerWidth)*0.85,
										height:'auto'
									})
							    })
						}else{
							_this.showGrap="暂无数据";
						}    
			          }
			        })
			        .catch(err => {
			          console.log(err);
			          _this.showGrap="暂无数据";
			       });
		    	}
			},
		},
	    mounted(){
	    	this.loadAll();
	    }
	}
</script>
<style scoped="">
	.zstp_fg_page3 .cont{
		width: 90%;
	    margin: 30px auto;
	    min-height: 780px;
	    padding: 40px;
	    box-sizing: border-box;
	} 
	.zstp_fg_page3 .input-with-select .el-input-group__prepend {
	    background-color: #fff;
	}
</style>



